:root {
  --footer-text-color: #fff;
  --font-family: "Epilogue", sans-serif;
  --footer-header-background-color: #1e2827;
  --trasform: 250ms cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, p {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.footer-container {
  background-color: var(--footer-header-background-color);
  font-family: var(--font-family);
  color: var(--footer-text-color);
  list-style-type: none;
}

.footer-section {
  padding: 40px 20px 20px;
}

.first-row {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  display: flex;
}

.footer-logo {
  width: fit-content;
  flex-direction: column;
  display: flex;
}

.footer-logo-svg {
  width: 40px;
  height: 40px;
}

.tel-class-footer {
  padding-top: 40px;
}

.f-section-link {
  text-align: left;
  width: max-content;
  flex-direction: row;
  align-content: center;
  justify-content: end;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.f-section-link li {
  padding-bottom: 14px;
}

.f-section-link a {
  text-transform: uppercase;
  color: var(--footer-text-color);
  text-decoration: none;
}

.address-footer {
  padding-top: 20px;
  display: block;
}

.social-links-footer {
  padding-top: 40px;
  padding-bottom: 40px;
}

.social-ellipse {
  fill: var(--footer-header-background-color);
  border-radius: 10%;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding-right: 14px;
  display: inline-block;
}

.social-icon {
  width: 40px;
  height: 40px;
  background-color: var(--footer-header-background-color);
  fill: var(--footer-text-color);
  border: 1px solid #5e6265;
  border-radius: 20%;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  display: flex;
}

.icon {
  width: 40px;
  height: 40px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 8px;
  display: block;
}

.icon svg {
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
}

.icon use {
  width: 24px;
  height: 24px;
}

.footer-policy-terms {
  flex-direction: row;
  gap: 14px;
  padding-top: 14px;
  display: flex;
}

.last-row {
  color: #c6cdd1;
}

@media screen and (min-width: 768px) {
  .footer-container {
    height: 476px;
  }

  .footer-section {
    padding: 80px 32px 20px;
  }

  .first-row-tablet {
    flex-direction: row;
    justify-content: space-between;
    display: flex;
  }

  .first-row {
    justify-content: space-evenly;
    gap: 142px;
    display: flex;
  }

  .social-links-footer {
    padding-top: 0;
  }

  .footer-policy-terms {
    padding: 0;
  }

  .last-row {
    align-content: flex-end;
    justify-content: space-between;
    padding-top: 152px;
    display: flex;
  }

  .social-ellipse {
    padding-right: 16px;
  }
}

@media screen and (min-width: 1158px) {
  .footer-section {
    padding: 104px 140px 32px 132px;
  }

  .first-row {
    width: 55%;
    justify-content: space-between;
    align-items: flex-start;
  }
}

